import { NavBar, Image, Toast,Popup ,Dialog} from 'antd-mobile'
import styles from './nftdetail.module.scss'
import {  useLocation, useParams } from 'react-router-dom'
import { useEffect, useState } from 'react'
import { useStore } from '@/store'
import { observer } from 'mobx-react-lite'
import Icon from '@/components/Icon/icon'
import { useNavigate } from 'react-router-dom'
import request from '@/utils/request'
import { getToken } from "@/utils/token"



const NFTDetail = () => {

  const nftInfoParams:any  = useLocation().state 
  const { id } = useParams()
  const navigate  = useNavigate()
  const userphone=getToken().userphone
  const [visible,setVisible] = useState(false)
  const [disable1,setDisable1] = useState(false)
  const [disable2,setDisable2] = useState(false)

  //回到上一页
  const back=()=>{
    window.history.back()
  }

  //获取到上一级组件findnftItem传来的nfttokenid
  const { communityname } = useParams()

  const {nftInfoStore}= useStore()
       
  // 获取详情数据
  useEffect(() => { 
    const runAction = async()=>{
      await nftInfoStore.getNFTInfo(communityname!,id!,userphone!)
      
    }
    runAction()
  }, [nftInfoStore,communityname,id,userphone ])

  const { userStore  } = useStore()

  //获取用户实名认证和钱包余额信息
  useEffect(() => {
    const runAction = async () => {
    //await userStore.getUserAuthInfo(userphone!)
   
    }
    runAction()
  }, [userStore, userphone])


   const navigeCommunityDetail = () => {
    navigate(`/island/${communityname}`)
  }


 
//点击喜欢图标触发的行为
  async function clickLike(){
    //取消点赞，从数据库中删除对应记录
    if(nftInfoStore.nftInfo.islike === 1  ){
      nftInfoStore.nftInfo.islike = 0
      await nftInfoStore.deleteLikeNft(userphone!,id!,nftInfoStore.nftInfo.communityid!)
      nftInfoStore.nftInfo.nftlikenumber=String(parseInt(nftInfoStore.nftInfo.nftlikenumber)-1)
    }
    //点赞，在数据库中Insert对应记录  
    else if(nftInfoStore.nftInfo.islike === 0 ){
      nftInfoStore.nftInfo.islike = 1
      await nftInfoStore.insertLikeNft(userphone!,id!,nftInfoStore.nftInfo.communityid!)
      nftInfoStore.nftInfo.nftlikenumber=String(parseInt(nftInfoStore.nftInfo.nftlikenumber)+1)
    }
    else{
      Toast.show("系统繁忙")
    }
  }
  

  //点击取消售卖触发的行为
  async function clickCancel() {

    setDisable1(true)

    const res = await nftInfoStore.cancelSellNft(userphone,nftInfoStore.nftInfo.ownerusername,nftInfoStore.nftInfo.createusername,nftInfoStore.nftInfo.nfttokenid,nftInfoStore.nftInfo.communityid)
  
    if(res){(
      Toast.show({
        content: "取消寄售",
        duration: 600,
        afterClose: () => {
          // 返回
          navigate('/profile')
        }
      })

    )}
  
}

async function Pay(){
  setDisable2(true)
  Toast.show("已提交，请等待")

  if(userStore.myIslandWallet_Balance.islandwallet<nftInfoStore.nftInfo.nftprice){
    Dialog.alert({
      content: 'Island钱包余额不足，请前往个人页面-我的Island钱包进行充值',
      confirmText:'前往Island钱包',
      onConfirm: () => {
          navigate('/islandwallet')
      },
    })
  }
  else{
   
   Toast.show({
     icon: 'loading',
     content: "交易正在进行中，请稍后在我的页面-我的持有中查看",
   })
  
   const res = await nftInfoStore.transferNft(userphone,nftInfoStore.nftInfo.nfttokenid,nftInfoStore.nftInfo.communityid,nftInfoStore.nftInfo.owneruserphone,nftInfoStore.nftInfo.nftprice)
    if(res.status===0)
    {
       setTimeout(()=>{  
          navigate('/profile') 
        },0)
    }
   else{
    Toast.show(res.msg)
   }
  
 } 
}


  const NftDetailFootBar =()=>{

    //如果此商品不在售卖中
    if(nftInfoStore.nftInfo.issell===0){
      return(
        <div className={styles.footer}>
             
        <div className='btn1'>
          <div className='price'>￥ {nftInfoParams.nftprice} 已售出</div>
          {/* <button className='nft-buy-button' onClick={()=>{setVisible(true)}}>购买 </button> */}
          </div>
          </div>
      )
    }

    //商品在售卖中
    else {
      //不可售卖
      if(nftInfoStore.nftInfo.cansell===0){
        return(
          <div className={styles.footer}>
            
            <div className='btn'>
            <div className='price'></div>
              <button className='nft-buy-button' >非售卖</button>
            </div>
          </div>
        )
      }
      else{
        //本人的NFT，正在售卖中
        if(nftInfoStore.nftInfo.owneruserphone===userphone && nftInfoStore.nftInfo.issell===1 ){
          return(
            <div className={styles.footer}>
              
              <div className='btn'>
                <div className='price'>￥ {nftInfoParams.nftprice}</div>
                {/* <button className='nft-buy-button' disabled={disable1} onClick={clickCancel}>取消售卖</button> */}
              </div>
            </div>
          )
        }
        else{
          //别人在售卖中的NFT
          return(
            <div  className={styles.footer}>
             
              <div className='btn'>
                <div className='price'>￥ {nftInfoParams.nftprice} 购买</div>
                {/* <button className='nft-buy-button' onClick={()=>{setVisible(true)}}>购买 </button> */}
                </div>
                <Popup
                visible={visible}
                onMaskClick={() => {
                  setVisible(false)
                }}
                bodyStyle={{ height: '40vh' }}
              >
                <div className={styles.popup}>
                  <div className="price1">
                    待付款
                  </div>
                  <div className="price2">
                    ￥ {nftInfoParams.nftprice}
                  </div>


                  <div className="price3">
                    Island钱包余额：{userStore.myIslandWallet_Balance.islandwallet}元
                  </div>
                  
                  <button 
                    disabled={disable2}
                    onClick={Pay}
                    className="btn"
                  >
                    确认支付
                  </button>
                </div>
              </Popup> 
             
            </div>
          )
        }

      }
      
    }
  }
  

  

  return (
    <div className={styles.root}>
      <NavBar className="navbar" onBack={back}>作品详情</NavBar>
        <div className="nft-image">
          <Image className='img' lazy src={nftInfoParams.nftimguri} alt="NFT图片" ></Image>     
        </div>
        <div className="nft-info">
          <div className='nft-name'>{nftInfoStore.nftInfo.nftname}</div>
          <div className="nft-like" > 
            <Icon  type={nftInfoStore.nftInfo.islike===1?"like_red":"like_black"}
            onClick={clickLike}
            ></Icon>
            <div className='likenumber'>{nftInfoStore.nftInfo.nftlikenumber}</div>
          </div>
        </div>

        <div className='user-info'>
          <div className='owner'>
            <div className='inner'>

           
            <div className='userimage'>
              <img src={nftInfoStore.nftInfo.ownerprofilephoto} alt=''></img>
            </div>
            <div className='userinfo'>
              <div className='remark'>持有者</div>
              <div className='username'>{nftInfoStore.nftInfo.ownerusername}</div>
            </div>
            </div>
          </div>

          <div className='author'>
          <div className='inner'>
            <div className='userimage'>
              <img src={nftInfoStore.nftInfo.profilephoto} alt=''></img>
            </div>
            <div className='userinfo'>
              <div className='remark'>创作者</div>
              <div className='username'>{nftInfoStore.nftInfo.createusername}</div>
            </div>
            </div>

          </div>
         
        </div>

        <div className='brandinfo' onClick={navigeCommunityDetail}>
            <div className='title' >所属品牌</div>
            <div className='communityname'>{nftInfoStore.nftInfo.communityname}</div>
            <div className='line'></div>
            <div className='title' >铸造信息</div>

            <div className='moreinfo'>
              <div className='titlename'>认证标识</div>
              <div className='content'>{nftInfoStore.nftInfo.nfttokenid}</div>
            </div>

            <div className='moreinfo'>
              <div className='titlename'>认证网络</div>
              <div className='content'>polygon</div>
            </div>

            <div className='moreinfo'>
              <div className='titlename'>认证标准</div>
              <div className='content'>ERC721</div>
            </div>

            <div className='moreinfo'>
              <div className='titlename'>合约地址</div>
              <div className='content'>0x0D0dcf5A8c54D773184668Ab55b2f1473b9904b4</div>
            </div>

            <div className='moreinfo'>
              <div className='titlename'>铸造时间</div>
              <div className='content'>{nftInfoStore.nftInfo.nftcreatetime}</div>
            </div>

            <div className='moreinfo'>
              <div className='titlename'>铸造耗时</div>
              <div className='content'>{nftInfoStore.nftInfo.nftcreatetime_cost} s</div>
            </div>

            <div className='moreinfo'>
              <div className='titlename'>存储地址</div>
              <div className='content'>{nftInfoStore.nftInfo.nftjsonuri}</div>
            </div>
            
        </div>

      {/*
          <div className='info2'>
            <div className='title'>存储地址</div>
            <div className='content'><a href={nftInfoStore.nftInfo.nftjsonuri}>{nftInfoStore.nftInfo.nftjsonuri}</a></div>
          </div>
       */}
      
        
      <div>
          <NftDetailFootBar />
      </div>

    </div>
  )
}

export default observer(NFTDetail)
