//登录页面


import { useNavigate} from 'react-router-dom'
import styles from './login.module.scss'
import { useStore } from '@/store'
import { Toast } from 'antd-mobile'
import { useState } from 'react' 
import request from '@/utils/request'
import useCountDown from './usecountdown'
import Icon from '@/components/Icon/icon'
import { fail } from 'assert'


const Login = () => {

  //用户输入的手机号
  const [userphone, setUserphone] = useState("");

  //用户输入的验证码
  const [code, setCode] = useState("");

  //倒计时  // 默认刚开始可以点击
  const [sendAble, setSendAble] = useState(1)

  // 设置初始值  定时器 定时器60秒，结束时候可以继续点击
  const { count, start } = useCountDown(60, () => setSendAble(1))

  //是否选中协议
  const [isselect, setIsselect] = useState(false);

  //点击发送验证码后调用的函数
  async function onSendCode(){

    //先对手机号进行正则校验，号码正确在发送验证码
    const rule = /^1[3|4|5|6|7|8|9][0-9]\d{4,11}$/;
    if(!userphone){
      Toast.show("请输入手机号")    //没有输入手机号提示请输入手机号
    }

    //手机号不符合正则判断，提示请输入正确的手机号   
    else if(!rule.test(userphone)){
      Toast.show("请输入正确的手机号")            
    } 
    
    //手机号验证成功
    else {       

      //发送校验码
      await getTextMessage(userphone)
    
      // 开启定时器  
      start()
      // 关闭定时器
      setSendAble(0)
    }

  }
      

  //调用后端接口，发送验证码
  async function getTextMessage(userphone:string){
    const res=await request.get(`/login/getTextMessage`,{
      params:{
          userphone
        }
      })
    Toast.show(res.data.message)
  }
   
    const navigate  = useNavigate()
    const { loginStore } = useStore()

    //登录操作
    async function onFinish(){
      if(!code){
        Toast.show("请输入验证码") 
      }
      
      else{
        // 调用登录异步方法
        const res = await loginStore.login(userphone,code)
          // 后续处理
          // console.log("test1-----------",res)
          if(res.data.state==='0')
          {
            //window.history.back()
            // 返回首页 之后要改
            //navigate('/')
            window.history.back()
            
            Toast.show({
                content: res.data.message,
                duration: 600
              })
          }else{
            Toast.show({
              content: res.data.message,
          })
        }
      }
      

    }


  return (
    <div className={styles.root}>
      <div className='bg'></div> 
      <div className="loginform">
        <div className='title'>
          <div className='name'>元岛</div>
          <div className='slogan'>岛向新潮流</div>
        </div>
        <div className='input1'>
          <div>+86</div>
          <input 
            placeholder='点击输入手机号'
            value={userphone}
            onChange={(e) => setUserphone(e.target.value) } 
            required={true}
          />
        </div>

        <div className='input2'>
          <input 
            required={true}
            placeholder='填写验证码'
            value={code}
            onChange={(e) => setCode(e.target.value) } 
          />
        
  
          <button className={sendAble===1?'button1':'button2'} onClick={onSendCode} disabled={sendAble===1?false:true}>
            { sendAble ? '发送验证码' : count + '秒'}
          </button>
        </div>


        <div className='notes'>
     
          <Icon type="community_select"/>
          

          
          阅读并同意  <a href='/useragreement'>用户协议</a> 

        </div>
        
        <div className='login'>
          <button onClick={onFinish}>
            登录
          </button>
        </div>
        
      </div>
    </div>
  )
}
export default Login