import "@/assets/Icon/svgicon"

// 组件 props 的类型
type Props = {
    // icon 的类型
    type: string | JSX.Element
    // 点击事件
    onClick?: () => void
  }
  
  const Icon = ({ type, onClick }: Props) => {
    return (
      <svg
        className="icon"
        aria-hidden="true"
        onClick={ onClick }
      >
        <use xlinkHref={`#${type}`}></use>
      </svg>
    )
  }
  
  export default Icon