
import { makeAutoObservable } from 'mobx'
import request from '@/utils/request'
import type { NFTInfo  } from '@/types/data'

class NFTInfoStore {
  // 声明nft详情信息
  nftInfo: NFTInfo
  constructor() {
    makeAutoObservable(this)
    this.nftInfo={} as NFTInfo
  }

  //获取NFT详情信息
  getNFTInfo = async (communityname: string,nfttokenid:string,userphone:string) => {
    const res = await request.get(`/nftDetail`,{
          params:{communityname,nfttokenid,userphone}
    })
    this.nftInfo = res.data.data
  }

  //点赞
  insertLikeNft= async(userphone:string,nfttokenid: string,communityid:number)=>{
    await request.post(`/insertLikeNft`,{
      userphone,
      nfttokenid,
      communityid
    })
  } 

  //取消点赞
  //点击取消喜欢,在数据库delete记录
    deleteLikeNft=async(userphone:string,nfttokenid: string,communityid:number)=>{
    await request.post(`/deleteLikeNft`,{
      userphone,
      nfttokenid,
      communityid
    })
  }

  //购买
  
//点击购买
    transferNft=async(userphone:string,nfttokenid:string,communityid:number,owneruserphone:string,nftprice:number)=>{
    const res = await request.post(`/transferNft`,{
      userphone,
      nfttokenid,
      communityid,
      owneruserphone,
      nftprice,
    })
    return res.data.data
  
  }


  //点击取消售卖，将价格置为0，issell置为0
    //在后端根据ownerusername，createusername判断是否将firstsell置0
    cancelSellNft=async(userphone:string,ownerusername:string,createusername:string,nfttokenid:string,communityid:number)=>{
  
    const res = await request.post(`/cancelSellNft`,{
      userphone,
      ownerusername,
      createusername,
      nfttokenid,
      communityid
    })
    return res
  }

  
}

export default NFTInfoStore
